@import "../../styles/variables";

body.lock {
  overflow: hidden;
}

.header {
  transition: all 0.8s linear 0.05s;
  display: block;
  position: fixed;
  width: 100%;
  // background-color: $colorSecondary;
  text-align: center;
  z-index: 300;

  img.logo {
    transition: all 0.8s linear 0.05s;
    float: left;
    display: block;
    height: 180px;
    padding: 0 0 15px;
    margin-right: 40px;
    // border: 1px solid white;
    z-index: 151;
    position: relative;
  }
  img.header__logo_scroll {
    display: none;
  }

  &__nav {
    z-index: 150;
    margin: auto;
    transform: translateY(70px);
    transition: all 0.8s linear 0.05s;
    font-size: 1.4em;
    line-height: 0.7em;
    font-family: $font-family-subtitle, sans-serif;
  }
  &__nav::before {
    content: "";
    color: #fff;
    display: block;
    background-image: url("/images/logo_without_letters_red_black.svg");
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100px;
    top: -10px;
    height: 60px;
    position: absolute;
    opacity: 0;
    transition: all 0.8s linear 0.05s;
  }

  &__list {
    display: flex;
    justify-content: space-evenly;
  }

  &__item {
    flex: 0 0 12%;
    padding: 3px;
    transition: all 0.3s linear 0.1s;
    text-transform: uppercase;
    display: block;
    color: white;
    letter-spacing: 0.05em;
    padding: 5px;
  }

  &__item:hover {
    transform: scale(1.2);
    text-shadow: 0 0 2px white;
    transition: all 0.3s linear 0.1s;
    border-top: 1px solid white;
    border-bottom: 1px solid white;
    // border-radius: 20px;
    // box-shadow: 0 0px 3px $colorTertiary, 0 0px 5px white;
  }

  &__nav_burger {
    display: none;
    width: 40px;
    height: 40px;
    position: absolute;
    transition: all 0.8s linear 0.05s;
    top: 35px;
    right: 10%;

    .burger__inner {
      background-color: #000;
      height: 4px;
      width: 100%;
      margin: 50% 0;
      transform: translateY(-50%);
      box-shadow: 0 4px 8px $colorTertiary;
      transition: all 0.8s linear 0.05s;
    }
  }

  &__nav_burger::before,
  &__nav_burger::after {
    content: "";
    display: block;
    height: 4px;
    width: 100%;
    background-color: #000;
    position: absolute;
    box-shadow: 0 4px 8px $colorTertiary;
  }

  &__nav_burger::before {
    top: 0;
    transition: all 0.8s linear 0.05s;
  }

  &__nav_burger::after {
    transition: all 0.8s linear 0.05s;
    bottom: 0;
  }
}

.scroll.header {
  background-color: $colorSecondary;
  transition: all 0.8s linear 0.05s;
  position: fixed;
  width: 100%;
  z-index: 300;
  img.logo.scroll {
    transition: all 0.8s linear 0.05s;
    opacity: 0;
    height: 60px;
    padding: 0 0 7px;
  }

  &__nav {
    transition: all 0.8s linear 0.05s;
    transform: translateY(15px);
    // font-size: 1.3em;
  }
  &__nav::before {
    opacity: 1;
    transition: all 0.8s linear 0.05s;
  }
}

@media (max-width: 1100px) {
  .header {
    img.logo {
      height: 120px;
      margin-right: 0;
    }

    &__nav {
      transform: translateY(45px);
      font-size: 1.1em;
    }

    &__item {
      a {
        padding: 3px;
      }
    }
  }
}

@media (max-width: 700px) {
  .header {
    img.logo {
      height: 100px;
    }

    &__nav_burger {
      display: block;
    }

    &__nav_burger.dropped {
      transition: all 0.8s linear 0.05s;

      .burger__inner {
        transform: scale(0);
        transition: all 0.8s linear 0.05s;
      }
    }

    &__nav_burger.dropped::before {
      transform: translateY(17.5px) rotate(45deg);
      transition: all 0.8s linear 0.05s;
    }

    &__nav_burger.dropped::after {
      transform: translateY(-17.5px) rotate(-45deg);
      transition: all 0.8s linear 0.05s;
    }

    &__nav {
      position: absolute;
      display: block;
      top: 45px;
      left: 250%;
    }

    &__list {
      display: block;
      background-color: #ce4709b9;
      text-shadow: 0 3px 10px $colorPrimary;
      box-shadow: 0 5px 5px $colorSecondary;
      border-radius: 0 0 50% 50%;
      padding-bottom: 15px;
    }

    &__item {
      background: none;
      border-radius: none;
      text-align: center;
      margin: 0;
      padding: 0;

      a {
        color: #fff;
        border: none;
        padding: 20px;
      }
    }
    &__item:hover {
      box-shadow: none;
    }
  }

  .scroll.header {
    &__nav {
      width: 100%;
      font-size: 1.5em;
      line-height: 0.6em;
    }

    &__nav.dropped {
      left: 0;
    }

    &__nav_burger {
      top: 20px;
      transition: all 0.8s linear 0.05s;
    }

    img.logo.scroll {
      height: 80px;
    }
  }
}
